import {CustomerLogin} from 'src/app/core/models/customer/login/request/customer-login.viewmodel';
import {ActionType, createAction, props} from '@ngrx/store';


export const LOGIN = '@Authen/CustomerLogin';
export const LOGIN_SUCCESS = '@Authen/CustomerLoginSuccess';
export const LOGIN_FAILED = '@Authen/CustomerLoginFailed';
export const LOGIN_RESET = '@Authen/CustomerLoginReset';
export const LOGOUT_SUCCESS = '@Authen/CustomerLogoutSuccess';
export const LOGOUT_FAILED = '@Authen/CustomerLogutFailed';

export const customerLoginAction = createAction(LOGIN, props<{customerLogin: CustomerLogin}>());
export const customerLoginSuccessAction = createAction(LOGIN_SUCCESS, props<{ message: string | null}>());
export const customerLoginFailedAction = createAction(LOGIN_FAILED, props<{message?: string}>());
export const customerLoginResetAction = createAction(LOGIN_RESET, props<{message?: string}>());

export const customerLogoutSuccessAction = createAction(LOGOUT_SUCCESS, props<{message: string | null}>());
export const customerLogoutFailedAction = createAction(LOGOUT_FAILED, props<{message?: string | null}>());

export type LoginActions =
	| ActionType<typeof customerLoginAction>
	| ActionType<typeof customerLoginSuccessAction>
	| ActionType<typeof customerLoginFailedAction>
	| ActionType<typeof customerLoginResetAction>
	| ActionType<typeof customerLogoutSuccessAction>
	| ActionType<typeof customerLogoutFailedAction>;
