/* eslint-disable prefer-arrow/prefer-arrow-functions */

import * as CustomerInforAction from './customer-infor.actions';
import {CustomerInforState} from './customer-infor.state';

const initialState: CustomerInforState = {
	customer: null,
	status: 'loading',
	message: ''
};
export function customerInforReducer(
	state: CustomerInforState = initialState,
	action: CustomerInforAction.LoginActions
): CustomerInforState {
	switch (action.type) {
		case CustomerInforAction.GET:
			return {...state};
		case CustomerInforAction.GET_SUCCESS:
			return {...state, customer: action.customer, status:'sucessed', message: action.message};
		case CustomerInforAction.GET_FAILED:
			return {...state, message: action.message};
		case CustomerInforAction.EDIT:
			return {...state, customer: action.customer};
		case CustomerInforAction.DELETE:
			return {...state};
		default:
			return {...state};
	}
}
