import { Injectable } from '@angular/core';
import {URL_String} from 'src/app/core/utils/URL/URL_String';
@Injectable({
	providedIn: 'root'
})
export class SellerUrlBase extends URL_String {
	readonly sellerUrl = this.hostBase + '/seller';
	readonly sellerUrlLogin: string = this.sellerUrl + '/seller/login';
	readonly sellerUrlRegister: string = this.sellerUrl + '/seller/register';
	constructor() {
		super();
	}
}
