import * as CustomerSettingAction from './customer-setting.actions';
import { CustomerSettingsState } from './customer-settings.state';
export const initialState: CustomerSettingsState = {
	language: 'customer-vn',
	theme: 'default'
};
export const customerSettingReducer=(
	state: CustomerSettingsState = initialState,
	action: CustomerSettingAction.CustomerSettingActions
): CustomerSettingsState => {
	switch (action.type) {
		case CustomerSettingAction.SETTING_CHANGE_LANGUAGE:
			return {...state, ...action};
		case CustomerSettingAction.SETTING_CHANGE_THEME:
			return {...state, ...action};
		default:
			return {...state};
	}
};
