/* eslint-disable prefer-arrow/prefer-arrow-functions */
import {CustomerLoginState} from './customer-login.state';
import * as LoginActions from './customer-login.actions';

const initialState: CustomerLoginState = {
	status: 'loading',
	message: '',
	isAuthenticated: false
};

export function customerLoginReducer(
	state: CustomerLoginState = initialState,
	action: LoginActions.LoginActions
): CustomerLoginState {
	switch (action.type) {
		case LoginActions.LOGIN:
			return {...state, status:'loading'};
		case LoginActions.LOGIN_SUCCESS:
			return {
				...state,
				status: 'loaded',
				isAuthenticated: true,
				message: action.message
			};
		case LoginActions.LOGIN_FAILED:
			return {...state, status: 'loaded', message: action.message};
		case LoginActions.LOGIN_RESET:
			return {...state, isAuthenticated: false, status: 'loading', message: action.message};
		case LoginActions.LOGOUT_SUCCESS:
			return {...state, message: action.message, isAuthenticated: false, status:'loaded'};
		case LoginActions.LOGOUT_FAILED:
			return {...state, message: action.message, isAuthenticated: true, status:'loaded'};
		default:
			return {...state};
	}
}
