import {ActionType, createAction, props} from '@ngrx/store';
import {Customer} from 'src/app/core/models/customer/customer.model';

export const GET = '@CustomerInfor/GetCustomerInfor';
export const GET_SUCCESS = '@CustomerInfor/GetCustomerInforSuccess';
export const GET_FAILED = '@CustomerInfor/GetCustomerInforFailed';
//
export const EDIT = '@CustomerInfor/EditCustomerInfor';
//
export const DELETE = '@CustomerInfor/DeleteCustomerInfor';

export const customerGetInforAction = createAction(GET);
export const customerGetInforSuccessAction = createAction(GET_SUCCESS, props<{customer: Customer; message?: string}>());
export const customerGetInforFailedAction = createAction(GET_FAILED, props<{message?: string}>());
export const customerEditInforAction = createAction(EDIT, props<{customer: Customer}>());
export const customerDeleteInforAction = createAction(DELETE, props<{message?: string}>());

export type LoginActions =
	| ActionType<typeof customerGetInforAction>
	| ActionType<typeof customerGetInforSuccessAction>
	| ActionType<typeof customerGetInforFailedAction>
	| ActionType<typeof customerEditInforAction>
	| ActionType<typeof customerDeleteInforAction>;
