import {SellerSettingsState} from 'src/app/core/store/seller/setting/seller-settings.state';
import * as SellerSettingAction from './seller-setting.actions';
export const initialState: SellerSettingsState = {
	language: 'seller-vn',
	theme: 'default'
};
export function sellerSettingReducer(
	state: SellerSettingsState = initialState,
	action: SellerSettingAction.SellerSettingActions
): SellerSettingsState {
	switch (action.type) {
		case SellerSettingAction.SETTING_CHANGE_LANGUAGE:
			return {...state, ...action};
		case SellerSettingAction.SETTING_CHANGE_THEME:
			return {...state, ...action};
		default:
			return {...state};
	}
}
