/* eslint-disable prefer-arrow/prefer-arrow-functions */
import {LoginState} from './login.state';
import * as LoginActions from './login.actions';
import {Status} from 'src/app/core/models/common/status/status.model';

const initialState: LoginState = {
	user: null,
	status: Status.idle,
	message: '',
	isAuthenticated: false
};
export function loginReducer(state: LoginState = initialState, action: LoginActions.LoginActions): LoginState {
	switch (action.type) {
		case LoginActions.LOGIN:
			return {...state, status: Status.loading, isAuthenticated: false};
		case LoginActions.LOGIN_SUCCESS:
			return {user: action.user, message: action.message, status: Status.idle, isAuthenticated: true};
		case LoginActions.LOGIN_FAILED:
			return {status: Status.error, message: action.message, isAuthenticated: false};
		case LoginActions.LOGIN_RESET:
			return {
				isAuthenticated: initialState.isAuthenticated,
				status: initialState.status,
				message: initialState.message,
				user: initialState.user
			};
		default:
			return {...state, status: Status.error};
	}
}
