import { Theme } from './seller-settings.state';
import {ActionType, createAction, props} from '@ngrx/store';
import {Language} from 'src/app/core/store/seller/setting/seller-settings.state';

export const SETTING_CHANGE_LANGUAGE = '\'[Settings] Seller Change Language\',';
export const SETTING_CHANGE_THEME = '[Settings] Seller Change Theme';



export const actionSettingsChangeLanguage = createAction(SETTING_CHANGE_LANGUAGE, props<{language: Language}>());
export const actionSettingsChangeTheme = createAction(SETTING_CHANGE_THEME, props<{theme: Theme}>());
export type SellerSettingActions =
	| ActionType<typeof actionSettingsChangeLanguage>
	| ActionType<typeof actionSettingsChangeTheme>;
