import {LocalStorageService} from 'src/app/core/services/comon-services/local-storage-service/localstorage.service';
import {ApiResult} from '../../../models/common/ApiResult';
import {HttpClient} from '@angular/common/http';
import {AdminLogin} from 'src/app/core/models/admin/authenticate/AdminLogin';
import {Injectable} from '@angular/core';
import {map} from 'rxjs/operators';
import {AdminLoginRespond} from 'src/app/core/models/admin/authenticate/AdminLoginRespond';
import {AdminUrlAccount} from 'src/app/core/utils/URL/admin-url/admin-url-account';
@Injectable({
	providedIn: 'root'
})
export class AccountService {
	constructor(
		private http: HttpClient,
		private adminUrlAccountString: AdminUrlAccount,
		private localStorageService: LocalStorageService
	) {}

	adminlogin(adminlogin: AdminLogin) {
		return this.http
			.post<ApiResult<AdminLoginRespond>>(
				this.adminUrlAccountString.adminUrlLogin, //"https://localhost:5005/api/admin/Admin/login",
				{
					email: adminlogin.email,
					passWord: adminlogin.passWord,
					remember: adminlogin.remember,
					urlReturn: adminlogin.urlReturn
				},
				{
					responseType: 'json'
				}
			)
			.pipe(
				map(
					(result: ApiResult<AdminLoginRespond>) => {
						if (result.isSuccessed === true) {
							if (result.isSuccessed && result.resultObj) {
								if (result.resultObj.tokenValue) {
									this.localStorageService.removeItem('admin-token');
									this.localStorageService.removeItem('admin-infor');
									this.localStorageService.setItem('admin-token', result.resultObj.tokenValue);
									//this.adminSubject.next(customer);
									// if (this.jwtTokenService.getExpiryTime(result.resultObj.tokenValue)) {
									// 	this.appCookieService.set(
									// 		'admin-token',
									// 		result.resultObj.tokenValue,
									// 		parseInt(this.jwtTokenService.getExpiryTime(result.resultObj.tokenValue), 10)
									// 	);
									// 	this.appCookieService.set(
									// 		'admin-infor',
									// 		JSON.stringify(result.resultObj),
									// 		parseInt(this.jwtTokenService.getExpiryTime(result.resultObj.tokenValue), 10)
									// 	);
									// }

									return result;
								} else {
									return result;
								}
							} else {
								return result;
							}
						} else {
							return result;
						}
					},
					(error: Error) => {
						console.log(error);
					}
				)
			);
	}
}
