import {CustomerUrlBase} from './../../utils/URL/customer-url/customer-url-base';
import {SellerUrlBase} from './../../utils/URL/seller-url/seller-url-base';
import {Injectable} from '@angular/core';
import {HttpRequest, HttpHandler, HttpEvent, HttpInterceptor, HttpErrorResponse} from '@angular/common/http';
import {Observable} from 'rxjs';
import {AuthService} from '../../services/auth/auth.service';
import {tap} from 'rxjs/operators';
import {Router} from '@angular/router';
import {CustomerAppState} from '../../store/customer/customer-app.state';
import {Store} from '@ngrx/store';
import {customerLoginResetAction} from '../../store/customer/login/customer-login.actions';
import {AdminUrlBase} from '../../utils/URL/admin-url/admin-url-base';
import {LocalStorageService} from '../../services/comon-services/local-storage-service/localstorage.service';

@Injectable()
export class AppAuthInterceptor implements HttpInterceptor {
	constructor(
		public router: Router,
		private authService: AuthService,
		private sellerUrlString: SellerUrlBase,
		private adminUrlString: AdminUrlBase,
		private customerUrlString: CustomerUrlBase,
		private store: Store<CustomerAppState>,
		private localStore: LocalStorageService
	) {}

	intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<unknown>> | undefined {
		if (request.url.includes(this.sellerUrlString.sellerUrl)) {
			const isAuthenticated = this.authService.isAuthenticated('seller-token');

			if (isAuthenticated) {
				const tokenValue = this.localStore.getItem('seller-token');
				const cloned = request.clone({
					headers: request.headers.set('Authorization', 'Bearer ' + tokenValue)
				});
				return next.handle(cloned);
			} else {
				return next.handle(request).pipe(
					tap({
						error: (err: any) => {
							if (err instanceof HttpErrorResponse) {
								if (err.status === 401) {
									return;
									//mỗi khi gửi token đi, nếu không có token hoặc token hết hạn thì dispatch logout
									// this.localStorageService.removeItem('customerlogin');
									// this.store.dispatch(customerLoginResetAction({message: 'logout'}));
								} else {
									return;
								}
							} else {
								return;
							}
						}
					})
				);
			}
		}
		if (request.url.includes(this.customerUrlString.customerUrl)) {
			const isAuthenticated = this.authService.isAuthenticated('customer-token');

			if (isAuthenticated) {
				//const tokenValue = this.appCookieService.get('customer-token');
				const tokenValue = this.localStore.getItem('customer-token');
				const cloned = request.clone({
					headers: request.headers.set('Authorization', 'Bearer ' + tokenValue)
				});
				return next.handle(cloned);
			} else {
				return next.handle(request).pipe(
					tap({
						error: (err: any) => {
							if (err instanceof HttpErrorResponse) {
								if (err.status === 401) {
									//mỗi khi gửi token đi, nếu không có token hoặc token hết hạn thì dispatch logout
									return;
									//this.localStorageService.removeItem('customerlogin');
									this.store.dispatch(customerLoginResetAction({message: 'logout'}));
								} else {
									return;
								}
							} else {
								return;
							}
						}
					})
				);
			}
		}
		if (request.url.includes(this.adminUrlString.adminUrl)) {
			const isAuthenticated = this.authService.isAuthenticated('admin-token');

			if (isAuthenticated) {
				const tokenValue = this.localStore.getItem('admin-token');
				const cloned = request.clone({
					headers: request.headers.set('Authorization', 'Bearer ' + tokenValue)
				});
				return next.handle(cloned);
			} else {
				return next.handle(request);
			}
		} else {
			return next.handle(request);
		}
	}
}
