import { customerInforReducer } from 'src/app/core/store/customer/infor/customer-infor.reducer';
import {customerLoginReducer} from 'src/app/core/store/customer/login/customer-login.reducer';
import {adminSettingReducer} from '../admin/setting/admin-setting.reducer';
/* eslint-disable @typescript-eslint/naming-convention */
import {ActionReducerMap, MetaReducer} from '@ngrx/store';
import {AppState} from './app.state';
import {customerSettingReducer} from '../customer/setting/customer-setting.reducer';
import {loginReducer} from '../seller/login/login.reducer';
import {sellerSettingReducer} from '../seller/setting/seller-setting.reducer';
import {initStateFromLocalStorage} from './init-state-from-local-storage.reducer';

//export const selectLoginState = createFeatureSelector<SellerAppState, LoginState>('sellerLogin');

/**
 * Our state is composed of a map of action reducer functions.
 * These reducer functions are called with each dispatched action
 * and the current or initial state and return a new immutable state.
 */
export const reducers: ActionReducerMap<AppState> = {
	sellersettings: sellerSettingReducer,
	sellerLogin: loginReducer,
	customersettings: customerSettingReducer,
	adminsettings: adminSettingReducer,
	customerlogin: customerLoginReducer,
	customerinfor:customerInforReducer,
};
/**
 * By default, @ngrx/store uses combineReducers with the reducer map to compose
 * the root meta-reducer. To add more meta-reducers, provide an array of meta-reducers
 * that will be composed to form the root meta-reducer.
 */
export const metaReducers: MetaReducer<AppState>[] = [initStateFromLocalStorage];

