import {LocalStorageService} from './core/services/comon-services/local-storage-service/localstorage.service';
/*CORE SYSTEM */
import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';
import {BrowserAnimationsModule} from '@angular/platform-browser/animations';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {TranslateHttpLoader} from '@ngx-translate/http-loader';
import {HttpClient, HttpClientModule, HTTP_INTERCEPTORS} from '@angular/common/http';
import {TranslateLoader, TranslateModule} from '@ngx-translate/core'; //
//NGRX
import {StoreDevtoolsModule} from '@ngrx/store-devtools';
import {StoreModule} from '@ngrx/store';
import {EffectsModule} from '@ngrx/effects';
/*CORE APP */
import {AppRoutingModule} from './app-routing.module';
import {URL_String} from './core/utils/URL/URL_String';
import {AppAuthInterceptor} from './core/interceptor/seller/app-auth.interceptor';
import {AccountService} from './core/services/auth/admin/account.service';
import {MatSnackBarComponent} from './core/components/mat-snack-bar/mat-snack-bar.component';
/*CORE GUI MODULE */
import {MaterialModule} from './core/utils/material.module';
import {NgZorroAntdModule} from './core/utils/ng-zorro-antd/ng-zorro-antd.module';
/*NG-ANT*/
import {IconsProviderModule} from './icons-provider.module';
import {NzLayoutModule} from 'ng-zorro-antd/layout';
import {NzMenuModule} from 'ng-zorro-antd/menu';
//INIT
//import {AppInitializerProvider} from '../app/features/customer/app-initializer.service';
//Env
import {environment} from '../environments/environment';
//Component
import {AppComponent} from './app.component';
//NOT IMPORT
import {JwtModule} from '@auth0/angular-jwt';
import {AppErrorHandler} from './core/error-handler/app-error-handler.service';
import {ChartsModule} from 'ng2-charts';
import {JWTTokenService} from './core/services/comon-services/jwt-token-service/jwttoken.service';
import {reducers, metaReducers} from './core/store/app/core.state';
import {TitleService} from './core/services/comon-services/title-service/title.service';
import en from '@angular/common/locales/en';
import {NZ_I18N, en_US} from 'ng-zorro-antd/i18n';
import {registerLocaleData} from '@angular/common';
import { TestModule } from './features/test/test.module';
registerLocaleData(en);
export const httpLoaderFactory=(http: HttpClient) => new TranslateHttpLoader(http); //
export const tokenGetter=() => localStorage.getItem('token');
@NgModule({
	declarations: [AppComponent, MatSnackBarComponent],
	imports: [
		// angular
		BrowserAnimationsModule,
		BrowserModule,
		FormsModule,
		ReactiveFormsModule,
		// app
		//TitleService,
		AppRoutingModule,
		IconsProviderModule,
		NzLayoutModule,
		NzMenuModule,
		HttpClientModule,
		MaterialModule,
		NgZorroAntdModule,
		//	CoreModule,

		TranslateModule.forRoot({
			loader: {
				provide: TranslateLoader,
				useFactory: httpLoaderFactory,
				deps: [HttpClient]
			}
		}),
		//StoreModule.forRoot({}, {}),
		EffectsModule.forRoot([]),
		StoreModule.forRoot(reducers, {metaReducers}),
		//StoreModule.forRoot(customerReducers,{customerMetaReducers}),
		StoreDevtoolsModule.instrument({maxAge: 25, logOnly: environment.production}),
		//RouterModule.forRoot({}),

		JwtModule.forRoot({
			config: {
				tokenGetter,
				allowedDomains: ['localhost:5005'],
				disallowedRoutes: []
			}
		}),
		ChartsModule,
		TestModule
	],

	providers: [
		{provide: NZ_I18N, useValue: en_US},
		AppErrorHandler,
		TitleService,
		JWTTokenService,
		AccountService,
		LocalStorageService,
		URL_String,
		{
			provide: HTTP_INTERCEPTORS,
			useClass: AppAuthInterceptor,
			multi: true
		}
	], //
	bootstrap: [AppComponent]
})
export class AppModule {
	constructor() {}
}


