import {createAction, props, ActionType} from '@ngrx/store';
import {Seller} from 'src/app/core/models/seller/seller.model';
import {SellerLogin} from 'src/app/core/models/seller/login/SellerLogin';

export const LOGIN = '@Authen/Login';
export const LOGIN_SUCCESS = '@Authen/LoginSuccess';
export const LOGIN_FAILED = '@Authen/LoginFailed';
export const LOGIN_RESET = '@Authen/LoginReset';

export const login = createAction(LOGIN, props<{userLogin: SellerLogin}>());
export const loginSuccess = createAction(LOGIN_SUCCESS, props<{user: Seller; message?: string}>());
export const loginFailed = createAction(LOGIN_FAILED, props<{message?: string}>());
export const loginReset = createAction(LOGIN_RESET);

export type LoginActions =
	| ActionType<typeof login>
	| ActionType<typeof loginSuccess>
	| ActionType<typeof loginFailed>
	| ActionType<typeof loginReset>;
