import {SellerAppState} from 'src/app/core/store/seller/seller-app.state';
import {ActionReducer, INIT, UPDATE} from '@ngrx/store';
import {LocalStorageService} from 'src/app/core/services/comon-services/local-storage-service/localstorage.service';
import {AppState} from './app.state';

export function initStateFromLocalStorage(reducer: ActionReducer<AppState>): ActionReducer<AppState> {
	// console.log('reducer truoc: ' + reducer);
	// console.log('count truoc: ' + count);
	return function (state, action) {
		// console.log('lan: ' + count);
		// count++;
		// console.log('initStateFromLocalStorage');
		// console.log('state');
		// console.log(state);
		// console.log('action');
		// console.log(action);
		const newState = reducer(state, action);
		// console.log('newState');
		// console.log(newState);

		// console.log('INIT.toString()');
		// console.log(INIT.toString());

		// console.log('UPDATE.toString()');
		// console.log(UPDATE.toString());

		if ([INIT.toString(), UPDATE.toString()].includes(action.type)) {
			//console.log('trong if ******************************');
			return {...newState, ...LocalStorageService.loadInitialState()};
		}
		//console.log('Ngoai if ******************************');
		return newState;
	};
}
export function clearState(reducer: ActionReducer<any>): ActionReducer<any> {
	return function (state: SellerAppState, action: any): SellerAppState {
		return reducer(state, action);
	};
}
