import {Injectable, ErrorHandler} from '@angular/core';
import {HttpErrorResponse} from '@angular/common/http';

import {environment} from '../../../environments/environment';

import {NzMessageService} from 'ng-zorro-antd/message';
import {appConfig} from '../configs/app.config';
import {throwError} from 'rxjs';
import { TranslateService } from '@ngx-translate/core';

/** Application-wide error handler that adds a UI notification to the error handling
 * provided by the default Angular ErrorHandler.
 */
@Injectable()
export class AppErrorHandler extends ErrorHandler {
	constructor(private message: NzMessageService,private appTranslateService: TranslateService,) {
		super();
	}

	handleError(error: Error | HttpErrorResponse) {
		super.handleError(error);
		throwError(error);
	}
	handleErrorWithNoti(error?: Error | HttpErrorResponse) {
		let displayMessage = 'Có Lỗi Xảy Ra, Vui Lòng Thử Lại';

		if (!environment.production) {
			displayMessage += ' ,Xem Console Thêm Chi Tiết';
		}

		this.message.error(this.appTranslateService.instant(displayMessage), {
			nzDuration: appConfig.snackBarDuration,
			nzPauseOnHover: true,
			nzAnimate: true
		});

		super.handleError(error);
		throwError(error);
	}
}
