import { AppTheme } from './../../app/app.state';

import {ActionType, createAction, props} from '@ngrx/store';
import { Language } from './admin-settings.state';


export const SETTING_CHANGE_LANGUAGE = '\'[Settings] Admin Change Language\',';
export const SETTING_CHANGE_THEME = '[Settings] Admin Change Theme';



export const actionSettingsChangeLanguage = createAction(SETTING_CHANGE_LANGUAGE, props<{language: Language}>());
export const actionSettingsChangeTheme = createAction(SETTING_CHANGE_THEME, props<{theme: AppTheme}>());
export type AdminSettingActions =
	| ActionType<typeof actionSettingsChangeLanguage>
	| ActionType<typeof actionSettingsChangeTheme>;
