import * as AdminSettingAction from './admin-setting.actions';
import { AdminSettingsState } from './admin-settings.state';
export const initialState: AdminSettingsState = {
	language: 'admin-vn',
	theme: 'default'
};
export const adminSettingReducer=(
	state: AdminSettingsState = initialState,
	action: AdminSettingAction.AdminSettingActions
): AdminSettingsState => {
	switch (action.type) {
		case AdminSettingAction.SETTING_CHANGE_LANGUAGE:
			return {...state, ...action};
		case AdminSettingAction.SETTING_CHANGE_THEME:
			return {...state, ...action};
		default:
			return {...state};
	}
};
