import {Injectable} from '@angular/core';
import jwt_decode from 'jwt-decode';

@Injectable()
export class JWTTokenService {
	//jwtToken: string;

	constructor() {}
	decodedToken: {[key: string]: string};
	// setToken(token: string) {
	// 	if (token) {
	// 		this.jwtToken = token;
	// 	}
	// }

	decodeToken(jwtToken: string) {
		if (jwtToken) {
			this.decodedToken = jwt_decode(jwtToken);
			return this.decodedToken;
		} else {
			return null;
		}
	}

	getDecodeToken(jwtToken: string) {
		return jwt_decode(jwtToken);
	}

	getUser(jwtToken: string) {
		this.decodeToken(jwtToken);
		return this.decodedToken ? this.decodedToken.displayname : null;
	}

	getEmailId(jwtToken: string) {
		this.decodeToken(jwtToken);
		return this.decodedToken ? this.decodedToken.email : null;
	}
	getRole(jwtToken: string) {
		this.decodeToken(jwtToken);
		return this.decodedToken
			? this.decodedToken['http://schemas.microsoft.com/ws/2008/06/identity/claims/role']
			: null;
	}

	getExpiryTime(jwtToken: string) {
		this.decodeToken(jwtToken);
		return this.decodedToken ? this.decodedToken.exp : null;
	}

	isTokenExpired(jwtToken: string): boolean {
		const expiryTime: string = this.getExpiryTime(jwtToken);
		if (expiryTime) {
			return 1000 * parseInt(expiryTime, 10) - new Date().getTime() < 5000;
		} else {
			return false;
		}
	}
}
