
import {NgModule} from '@angular/core';
import {Routes, RouterModule} from '@angular/router';

const routes: Routes = [

	{
		path: 'seller',
		loadChildren: () => import('./features/seller/seller.module').then((m) => m.SellerModule), data: {breadcrumb: 'Home'}
	},
	{
		path: 'admin',
		loadChildren: () => import('./features/admin/admin.module').then((m) => m.AdminModule)
	},
	{
		path: 'shipper',
		loadChildren: () => import('./features/shipper/shipper.module').then((m) => m.ShipperModule), data: {breadcrumb: 'Home'}
	},

	{
		path: '',
		loadChildren: () => import('./features/customer/customer.module').then((m) => m.CustomerModule)
	},

	{ path: 'test', loadChildren: () => import('./features/test/test.module').then(m => m.TestModule) },

];

@NgModule({
	imports: [RouterModule.forRoot(routes)],
	exports: [RouterModule]
})
export class AppRoutingModule {}
