import {Injectable} from '@angular/core';

@Injectable({
	providedIn: 'root'
})
export class AppCookieService {
	private cookieStore = {};

	constructor() {
		this.parseCookies(document.cookie);
	}
	// static loadInitialState() {
	// 	return Object.keys(document.cookie).reduce((state: any, name) => {
	// 		console.log('trong cookie');
	// 		//console.log(document.cookie);
	// 		// console.log(state);
	// 		// console.log(name);
	// 		//console.log(document.cookie);
	// 	}, {});
	// }

	public parseCookies(cookies = document.cookie) {
		this.cookieStore = {};
		if (!!cookies === false) {
			return;
		}
		const cookiesArr = cookies.split(';');
		for (const cookie of cookiesArr) {
			const cookieArr = cookie.split('=');
			
			this.cookieStore[cookieArr[0].trim()] = cookieArr[1];
		}
	}

	get(key: string) {
		this.parseCookies();
		return !!this.cookieStore[key] ? this.cookieStore[key] : null;
	}

	remove(key: string) {
		document.cookie = `${key} =; expires=Thu, 01 Jan 1970 00:00:00 UTC; path=/;`;

	}
	//old setting
	setCookieWthoutExpTime(key: string, value: string) {
		document.cookie = key + '=' + (value || '');
	}
	set(name: string, value: string, expireDays?: number) {
		const d: Date = new Date();
		d.setTime(expireDays * 1000);
		const expires = `expires=${d.toUTCString()}`;
		//const cpath: string = path ? `; path=${path}` : '';
		document.cookie = `${name}=${value}; ${expires}`;
	}
}
