import {Injectable} from '@angular/core';
import {Router} from '@angular/router';
import {JwtHelperService} from '@auth0/angular-jwt';
import {Store} from '@ngrx/store';
import {customerLoginResetAction} from '../../store/customer/login/customer-login.actions';
import {loginReset} from '../../store/seller/login/login.actions';
import {SellerAppState} from '../../store/seller/seller-app.state';
import {AppCookieService} from '../comon-services/cookie-service/app-cookie.service';
import {JWTTokenService} from '../comon-services/jwt-token-service/jwttoken.service';
import {LocalStorageService} from '../comon-services/local-storage-service/localstorage.service';

@Injectable({
	providedIn: 'root'
})
export class AuthService {
	constructor(
		public jwtHelper: JwtHelperService,
		private appCookieService: AppCookieService,
		private jwtTokenService: JWTTokenService,
		private store: Store<SellerAppState>,
		private localStore: LocalStorageService
	) {}
	/**
	 * isAuthenticated
 :boolean    */
	public isAuthenticated(tokenName: string): boolean {
		const tokenValue = this.localStore.getItem(tokenName);
		//exsit tokenValue
		if (tokenValue) {
			//if token is not exprired
			if (!this.jwtHelper.isTokenExpired(tokenValue)) {
				return true;
			} else {
				switch (tokenName) {
					case 'seller-token': {
						this.store.dispatch(loginReset());
						//this.store.dispatch(login({userLogin: null}));
					}
				}
				this.localStore.removeItem(tokenName);
				return false;
			}
		} else {
			return false;
		}
	}

	getRoleByToken(tokenName: string): boolean {
		switch (tokenName) {
			case 'seller-token': {
				const tokenValue = this.appCookieService.get(tokenName);
				if (this.jwtTokenService.getRole(tokenValue) === 'seller') {
					return true;
				} else {
					return false;
				}
			}
			case 'customer-token': {
				const tokenValue = this.appCookieService.get(tokenName);
				if (this.jwtTokenService.getRole(tokenValue) === 'customer') {
					return true;
				} else {
					return false;
				}
			}
			case 'admin-token': {
				const tokenValue = this.appCookieService.get(tokenName);
				if (this.jwtTokenService.getRole(tokenValue) === 'admin') {
					return true;
				} else {
					return false;
				}
			}
			default:
				return false;
		}
	}
	logout(tokenName: string, router?: Router) {
		switch (tokenName) {
			case 'customer':
				this.store.dispatch(customerLoginResetAction({message: 'logout'}));
				this.localStore.removeItem('customer-token');
				if (router) {
					router.navigate(['/login'], {queryParams: {returnUrl: router.routerState.snapshot.url}});
				}
				break;

			case 'seller':
			case 'admin':
			default:
		}
	}
}
